* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: aquamarine;
  color: #212121;
  line-height: 1.5;
  background-repeat: no-repeat;
  background-size:100%;
  margin:20px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
li{list-style-type:none}

.info{background-color: white;display: inline;}
.gallery{display: grid; grid-template-columns: 1fr 1fr 1fr; gap: 20px; object-fit: cover;  }
.photo-card{object-fit: cover; margin-top: 20px; object-fit: cover; }
.info{background-color: white;display: flex; gap:15px ; justify-content: center;width: 400px;  align-items: center;}
.info-item{display:grid; align-items: center; justify-content: center; margin-left: 10px;}

.image-item{width:400px;height: 500px;  border-style: solid; object-fit: cover ;border-radius: 20px; border-color: white;}
button{background-color: #4CAF50; /* Green */
  border: none;
  color: white;
 }